//        - off-canvas,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

$off-canvas-width:           360px !default; //   

.off-canvas {

  $block-name:                &; // #{$block-name}__element

  position: relative;
  overflow: hidden;

  //  
  @media (max-width: ($menu-desktop-width - 1)) {
    margin-top: 60px;
  }

  .js &--open {

    #{$block-name}__overlay {
      position: fixed;
      z-index: 99;
      opacity: 1;
      visibility: visible;
      transition: opacity $transition-time ease;
    }

    #{$block-name}__close {
      //  
      z-index: 20;
      // z-index: 1;
      opacity: 1;
      visibility: visible;
    }

    #{$block-name}__aside {
      // transform: translateX($off-canvas-width);
      //    
      transform: translateX(0);
      @media (min-width: $menu-desktop-width) {
        transform: translateX($off-canvas-width);
      }
      
    }

    // #{$block-name}__page-content {
    //   transform: translateX($off-canvas-width);
    // }
  }

  &__aside {
    max-height: 30vh;
    overflow-y: auto;
    backface-visibility: hidden;
    background-color: #fff;
    color: $tw-black;
    transition: transform $transition-time ease;
    //   
    // padding-top: 70px;
    @media (min-width: $menu-desktop-width) {
      padding-top: 70px;
    }



    .js & {
      display: block !important;
      position: fixed;
      // width: $off-canvas-width;
      //!!!  
      // height: 100%;
      height: calc(100% - 60px);
      // left: ($off-canvas-width * -1);
      max-height: none;
      z-index: 1010;
      //    
      width: 100%;
      left: 0;
      transform: translateX(-100%);
      @media (min-width: $menu-desktop-width) {
        width: $off-canvas-width;
        left: ($off-canvas-width * -1);
      }      
    }
  }

  &__menu {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__menu-item {

    &--separator {
      padding-bottom: 10px;
      border-bottom: 1px solid $gray;
      margin-bottom: 10px;
    }
  }

  &__menu-link {
    display: block;
    padding: 8px 14px;
    line-height: 1.2;
    color: inherit;
    text-decoration: none;

    &:hover,
    &:focus {
      color: inherit;
      text-decoration: none;
      background-color: $gray-darker;
    }
  }

  &__page-content {
    backface-visibility: hidden;
    transition: transform $transition-time ease;
  }

  &__overlay {
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
    opacity: 0;
    visibility: hidden;
  }

  &__close {
    position: absolute;
    //   
    // right: 32px; 
    right: 16px;
    //
    top: 32px;

    &::before,
    &::after {
      left: 0;
    }
  }

  &__toggler {
      // !!!       
      margin-right: 15px;

      @media (max-width: ($menu-desktop-width - 1)) {
        order: 3;
        padding: 0;
        margin: 0;
        width: 30px;
      }

      &::before,
      &::after {
        left: 0;
      }

      //
  }
}
