.img-form {
    margin-bottom: 40px;

    &--1 {

        @media (min-width: $screen-md) {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 16px;
        }    

        .img-form__img {
            margin-bottom: 8px;

            @media (min-width: $screen-md) {
                margin-bottom: 0;
            }
        }
    }

    &--2 {
        padding: 30px 20px;
        border-radius: 10px;
        background-color: $tw-slate-200;

        @media (min-width: $screen-md) {
            padding: 40px;
        }

        .btn {
            justify-self: start;
        }
    }

    &--3 {
        padding: 30px 20px;
        border-radius: 10px;
        background-color: $tw-slate-200;

        @media (min-width: $screen-md) {
            padding: 40px;
        }        
    }

    &__img {
        font-size: 0;
        line-height: 0;
        border-radius: 10px;
        overflow: hidden;

        @media (min-width: $screen-md) {
            margin-bottom: 0;
        }

        @media (min-width: $screen-3xl) {
            max-height: 450px;
        }    
    }

    .modal-form {
        @media (min-width: $screen-3xl) {
            max-height: 450px;
        }            
    }

    .ramform-text__input {
        padding: 10px 18px; 
        font-size: 16px;
        line-height: 31px;
        background-color: #fff;
        border-radius: 4px;
    }
}



.ramform .ramform-custom .ramform-text__input::placeholder, 
.ramform .ramform-custom .ramform-textarea__input::placeholder, 
.ramform .ramform-custom .ramform-select__input::placeholder {
	color: #686868;
	opacity: 1;
}