//        - main-nav,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.main-nav {

  $block-name: &; // #{$block-name}__element

  position: relative;
  z-index: 10;


  &--main {
    .page-header__inner & {
      margin-right: auto;
    }
  }

  &.nav--mobile {
    overflow-y: auto;
    height: 100%;
    font-size: 24px;
  }

  &__btn-cart {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    vertical-align: top;
    width: 48px;
    height: 48px;
    margin-left: 16px;
    border-radius: 50%;
    box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.1);
  }


  // &--open {
    // overflow-y: auto;
  //   height: 100%;
  // }
  
  &__container {
    width: 100%;
    padding: 0 16px;
    box-sizing: border-box;

    @media (min-width: 1280px) {
      width: 345px;
    }
  }

  &__banners {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-top: 8px;

    @media (min-width: 680px) {
      flex-direction: row;
    }

    @media (min-width: $menu-desktop-width) {
      flex-direction: column;
    }
  }

  &__inner {


    .js .nav--mobile & {
    //    -  
    //   display: block !important;
    //   max-height: 0;
    //   overflow-y: auto;
    //   transition: max-height 0.2s;

    //   & #{$block-name}__list--lvl-2 {
    //     max-height: 0;
    //   }

      & #{$block-name}__item--has-child {
        flex-direction: column;
      }

      @media (min-width: $screen-md) {
        display: flex;
        flex-direction: column;
        height: 100%;
      }
    }

    // .js .nav--mobile.main-nav--open & {
    //   max-height: 1000px;
    //   transition: max-height 0.6s;
    //   box-shadow: 0px 0px 150px rgba(0, 0, 0, 0.16);

    //   & #{$block-name}__list--lvl-2 {
    //     max-height: none;
    //   }
    // }
  }


  &__inner-wrapper {
    .nav--mobile & {
      @media (min-width: $screen-lg) {
        padding-top: 60px;
        height: calc(100% - 86px);
        box-sizing: border-box;
      }
    }
  }

  &__content-main {
    height: 100%;
    overflow-y: auto;
  }

  //  
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    background-color: #fff;

    @media (min-width: $screen-xxl) {

      .nav--main & {
        min-width: 710px;
        display: flex;
        justify-content: space-between;
      }
    }

    .nav--add & {
      padding-top: 15px;
    }

    .nav--mobile & {
      padding-top: 4px;
      max-height: 400px;
      overflow-y: scroll;
      line-height: 34px;

      &--lvl-2 {
        padding-top: 0;
        overflow-y: hidden;
      }
    }

    .nav--main &--lvl-2 {
      min-width: 260px;
      padding-bottom: 30px;
    }

    &--lvl-2 {
      border-radius: 8px;
    }

    //   
    @media (max-width: ($menu-desktop-width - 1)) {

      // /      JS
      .js .nav--main & {
        max-height: 0;
        overflow: hidden;
        transition: max-height 0.2s;

        &--lvl-2 {
          max-height: 0;
        }
      }

      .js .nav--main.main-nav--open & {
        max-height: 1000px;
        transition: max-height 0.6s;

        &--lvl-2 {
          max-height: none;
        }
      }
    }

    // /      JS
    .js .nav--add & {
      position: absolute;
      max-height: 0;
      overflow: hidden;
      transition: max-height 0.2s;

      &--lvl-2 {
        max-height: 0;
      }
    }

    .js .nav--add.main-nav--open & {
      max-height: 1000px;
      transition: max-height 0.6s;

      &--lvl-2 {
        max-height: none;
      }
    }

    //   
    @media (min-width: $menu-desktop-width) {

      .nav--main & {
        display: flex;
        flex-wrap: wrap;

        &--lvl-2 {
          display: block;
        }

        &--lvl-3 {
          display: block;
        }
      }
    }
  }


  &__item {
    position: relative;
    display: flex;

    &--active {

      &>#{$block-name}__link {
        // stylelint-disable-line  selector-combinator-blacklist
        color: #000;
      }
    }

    &--has-child {

      &>#{$block-name}__link span {
        position: relative;
        padding-right: 10px;

        &::after {
          @include arrow-down;

          // .nav--mobile & {
          //   top: 0.6em;
          //   transform: rotate(-135deg);
          // }
        }
      }

      @at-root &#{$block-name}__item--lvl-2 {

        &::after {
          transform: rotate(135deg) translateY(70%);
        }
      }
    }

    .nav--main & {

      & span {
        position: relative;
        display: flex;
        flex-direction: column;
      }

      & span::before {
        content: attr(data-content);
        font-size: inherit;
        font-weight: 700;
        height: 0;
        overflow: hidden;
        white-space: nowrap;
      }

      &--lvl-2 {
        font-size: 16px;
        line-height: 30px;
        color: $tw-gray-500;
      }
    }

    // .nav--mobile & {
    //   &--show-child > .main-nav__link span::after {
    //     transform: rotate(45deg) translateY(70%);
    //     right: -3px;
    //   }
    // }
  }

  &__item-icon {
    display: flex;
    margin-right: 5px;

    &--action {
      width: 20px;
      height: 20px;
      background-image: url('/img/hot-sale.png');
      background-size: contain;
    }
  }

  &__sublist-header {
    position: relative;
    display: flex;
    padding: 0.5em 0.9em;
    padding-top: 20px; //   
    padding-left: 30px; //   
    font-weight: 700;
    cursor: pointer;


    span {
      position: relative;
      padding-right: 10px;

      &::after {
        @include arrow-down;
        transition: transform $transition-time;

        .nav--main #{$block-name}__item--show-child>&,
        .nav--main #{$block-name}__item:hover & {
          transform: translateY(-50%) rotate(180deg);
        }
      }

    }

  }

  &__link {
    display: flex;
    align-items: center;
    padding: 0.5em 0.9em;
    text-decoration: none;
    color: inherit;

    &:hover,
    &:focus {
      color: inherit;
      opacity: 0.5;
      text-decoration: none;
    }

    @media (min-width: $screen-lg) {
      padding: 0.5em 0.7em;
    }

    @media (min-width: $screen-xl) {
      padding: 0.5em 0.9em;
    }

    .nav--add & {
      white-space: nowrap;
    }

    .nav--mobile & {
      padding: 4px 16px;
    }

    &--lvl-2 {
      white-space: nowrap;

      .nav--main & {
        padding-top: 0.2em;
        padding-bottom: 0.2em;
        padding-left: 30px;

        @media (max-width: ($menu-desktop-width - 1)) {
          padding-left: 2em;
        }
      }

      .nav--mobile & {
        padding-left: 2em;
      }
    }

    &--lvl-3 {
      white-space: nowrap;

      .nav--main & {
        @media (max-width: ($menu-desktop-width - 1)) {
          padding-left: 3em;
        }
      }

      .nav--mobile & {
        padding-left: 3em;
      }
    }
  }

  &__sublist-wrapper {

    .nav--main & {
      box-shadow: 0px 0px 14px rgba(0, 0, 0, 0.1);
      border-radius: 8px;
    }

    .nav--mobile & {
      max-height: 0;
      opacity: 0;
      overflow: hidden;
      transition: opacity $transition-time;
    }

    &--lvl-2 {

      @media (min-width: $screen-lg) {
        padding-bottom: 0;
      }

      //  
      .nav--main & {
        @media (min-width: $menu-desktop-width) {
          position: absolute;
          z-index: 1;
          top: -11px;
          left: -14px;
          height: 0;
          opacity: 0;
          overflow: hidden;
          transition: opacity $transition-time;
        }
      }

      // stylelint-disable
      .nav--main #{$block-name}__item--show-child>&,
      .nav--main #{$block-name}__item:hover & {
        @media (min-width: $menu-desktop-width) {
          height: auto;
          overflow: visible;
          opacity: 1;

          span::after  {
            transform: translateY(-50%) rotate(180deg);
          }
        }
      }

      .nav--mobile #{$block-name}__item--show-child>& {
        max-height: 200px;
        width: 300px;
        overflow-y: auto;
        opacity: 1;

        @media (min-width: 480px) {
          width: 400px;
        }
        @media (min-width: 600px) {
          width: 500px;
        }
        @media (min-width: $screen-lg) {
          width: 600px;
        }
      }

      // stylelint-enable
    }

    &--lvl-3 {

      padding-top: 5px;
      padding-bottom: 5px;

      @media (min-width: $screen-lg) {
          padding-top: 0;
        padding-bottom: 0;
      }

      .nav--main &,
      .nav--mobile & {
        @media (min-width: $menu-desktop-width) {
          position: absolute;
          z-index: 1;
          top: 0;
          left: 100%;
          opacity: 0;
          height: 0;
          overflow: hidden;
          transition: opacity $transition-time;
          // stylelint-enable
        }
      }

      // stylelint-disable
      .nav--main #{$block-name}__item--show-child>&,
      .nav--mobile #{$block-name}__item--show-child>& .nav--main #{$block-name}__item--lvl-2:hover & {
        @media (min-width: $menu-desktop-width) {
          height: auto;
          overflow: visible;
          opacity: 1;
        }
      }
    }
  }

  &__actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 12px;
    line-height: 34px;
  }

  &__banner {
    font-size: 0;
    line-height: 0;
  }

  &__add {
    display: flex;
    flex-wrap: wrap;
    margin-top: 32px;
    background-color: #EBEBEB;
    gap: 1px;
    border: 1px solid #EBEBEB;

    @media (min-width: $screen-md) {
      flex-wrap: nowrap;
    }

    @media (min-width: $menu-desktop-width) {
      flex-wrap: wrap;
    }
  }

  &__add-item {
    display: flex;
    align-items: center;
    flex-basis: 40%;
    flex-grow: 1;
    padding: 8px;
    background-color: #fff;
    // border: 1px solid transparent;
    transition: opacity $transition-time;

    &:hover {
      opacity: 0.5;
      // border-color: $tw-black;
    }

    @media (min-width: $screen-lg) {
      flex-basis: 100%;
    }
  }

  &__add-item-icon {
    margin-right: 12px;
  }

  &__add-item-text {
    font-size: 20px;
    line-height: 30px;
  }

  // 

  &__toggler {
    display: inline-block;
    border: none;
    background: none;
    z-index: 12;

    .nav--main & {
      @media (min-width: $menu-desktop-width) {
        display: none;
      }

    }

    .no-js & {
      display: none;
    }
  }
}


// @media (max-width: ($screen-xl - 1)) {
