.nav {
  @apply text-lg text-black py-0;


  &--aside {
    font-size: 18px;

    & .main-nav__link {
      padding-left: 60px;
    }
  }

  &--main {
    margin-right: auto;
  }

  &--mobile {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #fff;
  }

  &__info {
    padding: 16px;
    margin-top: 18px;
    background-color: #fff;

    @media (min-width: $screen-md) {
      margin-top: auto;
    }

    .contact_links {

    }

    .contact_links__messenger {
      transition: transform $transition-time;

      &:hover {
        transform: scale(1.1);
      }

    }

    .contact-info {
      @media (min-width: $menu-desktop-width) {
        display: none;
      }
    }

    .main-nav__btn-cart {
      @media (min-width: $screen-lg) {
        display: none;
      }
    }
  }
}
