//        - ramform,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.ramform {


  $block-name: &; // #{$block-name}__element

  $block-name: &; // #{$block-name}__element


  .ramform-custom {

    .ramform-item {
      padding-top: 10px !important;
      padding-bottom: 10px !important;
    }

    .ramform-item__bottom {
      position: absolute;
    }

    .ramform-item__error {
      margin-top: 0 !important;
    }

    .ramform-item__title {
      display: none !important;
    }

    .ramform-text__required {
      z-index: 1;
    }

    .ramform-text,
    .ramform-textarea {
      padding: 0 !important;
    }

    .ramform-text__input,
    .ramform-textarea__input,
    .ramform-select__input {
      display: inline-block !important;
      vertical-align: middle !important;
      width: 100% !important;
      height: 100% !important;
      margin: 0 !important;
      border: 1px solid $tw-gray-100 !important;
      border-radius: $border-radius !important;
      padding: 11px 18px !important;
      font-size: 16px !important;
      line-height: 26px !important;
      font-family: inherit !important;
      background-color: $tw-gray-100 !important;
      -moz-appearance: textfield !important; // stylelint-disable-line
      appearance: textfield !important;

      &::placeholder {
        color: $tw-gray-500;
        opacity: 1;
      }
    }

    .ramform-textarea__input {
      height: 230px !important;
    }

    .ramform-button {
      max-width: 100% !important;
      overflow: hidden !important;
      background-color: transparent;
    }

    .ramform-button__title {
      position: relative !important;
      padding: 0 !important;
      padding-left: 25px !important;
      white-space: nowrap;
      line-height: 1.2 !important;

      &::before {
        content: '';
        position: absolute;
        width: 20px !important;
        height: 20px !important;
        left: 0 !important;
        top: 50% !important;
        background: none !important;
        background-image: url('/img/btn-send-white.svg') !important;
        transform: translateY(-50%) !important;
      }

      &:hover {
        &::before {
          opacity: 1 !important;
        }
      }

      span {
        padding-left: 5px !important;
      }
    }


  } /* .ramform-custom END */

.contacts-form & {
  position: relative !important;

  .ramform-content {
    padding: 0 !important;
    max-width: none !important;
    margin-top: 10px !important;

    @media (min-width: $screen-lg) {
      margin-top: 0;
    }
  }
}

} /* .ramform END */

/*    */
.contacts-form,
.consult,
.product-main__special,
.aside-form,
.modal-form,
.product-actions,
.test-drive,
.tradein,
.kp-form {

  .ramform-close {
    position: absolute !important;
    opacity: 1 !important;
    width: 23px !important;
    height: 23px !important;
    right: 10px !important;
    top: 10px !important;
    background-color: transparent !important;
    cursor: pointer;

    &::before,
    &::after {
      height: 23px !important;
      width: 2px !important;
    }

    &:hover {
      opacity: 0.4 !important;
    }

    &:before,
    &:after {
      content: ' ';
      position: absolute !important;
      top: 0 !important;
      left: 50% !important;
      width: 2px !important;
      height: 23px !important;
      background-color: $tw-black !important;
      transform: translateX(-50%) !important;
    }

    &:before {
      transform: translateX(-50%) rotate(45deg) !important;
    }

    &:after {
      transform: translateX(-50%) rotate(-45deg) !important;
    }
  }

  .ramform-item__select-wrapper {
    position: relative;
    min-height: 50px;
    width: 100%;
    @apply text-base;
    background-color: #fff;
    border: 1px solid #f5f5f5 !important;
    border-radius: 4px !important;

    &::after {
      content: "";
      position: absolute;
      right: 17px;
      top: 50%;
      width: 6px;
      height: 4px;
      background-image: url(../img/angle-down.svg);
      background-size: contain;
      transform: translateY(-50%);
      border: 0;
      z-index: 10
    }
  }

  .ramform-item__select {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 11px 18px !important;
    font: inherit;
    font-size: inherit;
    line-height: inherit;
    font-family: inherit;
    background: none;
    background-color: transparent;
    border: none;
    appearance: none;
    outline: none;
    z-index: 1;

    &::-ms-expand {
      display: none;
    }
  }

}
/*   END*/



