//        - consult,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.consult {

  $block-name: &; // #{$block-name}__element

  padding: 30px;
  background-color: $tw-slate-200;
  border-radius: 10px;
  overflow: hidden;

  &__desc {
    margin-bottom: 20px;
    color: $tw-black;
    font-size: 24px;
    line-height: 34px;
  }

  .author {
    margin-bottom: 30px;

    &__img {
      width: 84px;
      height: 84px;
    }

    &__name {
      @apply text-lg;
    }
  }

  .author__info a {
    color: $tw-black !important;
    font-weight: 400 !important;
  }

  &--form {
    padding: 30px 20px;

    @media (min-width: $screen-md) {
      padding: 40px;
    }

    .consult__text {
      font-size: 16px;
      line-height: 26px;

      @media (min-width: $screen-md) {
        font-size: 18px;
        line-height: 28px;
      }
    }

    .author {
      margin-bottom: 20px;
    }

    .ramform-item {
      margin-bottom: 10px;
    }

    .ramform-text__input {
      width: 100%;

      &::placeholder {
        color: $tw-gray-500
      }
    }

    .ramform-button {
      padding: 12px 32px;
      background-color: $tw-blue_btn;
      color: #fff;
      border-color: $tw-blue_btn;      
    }



  }

  &--form-2 {
    padding: 0;
    padding-top: 30px;

    @media (min-width: $screen-md) {
      display: grid;
      grid-template-columns: auto 1fr;
      grid-template-rows: auto 1fr;
      width: 100%;
    }

    .author {
      grid-row-start: 1;
      grid-row-end: 3;
      margin-bottom: 20px;

      @media (min-width: $screen-md) {
        margin-bottom: 0;
        margin-right: 40px;
      }
    }

    .consult__text {
      margin-bottom: 16px;
      color: $tw-black;
      justify-self: start;
      font-size: 16px;
      line-height: 26px;

      @media (min-width: $screen-md) {
        font-size: 18px;
        line-height: 28px;
      }
    }  

    .consult__text-inner {
      display: flex;
      align-items: center;
    }

    .btn {
      padding: 11px 31px;
    }
  }

  &--form-3 {
    padding: 0;
    padding-top: 30px;

    @media (min-width: $screen-md) {
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      gap: 0 10px;
    }

    .consult__desc {
      grid-column-start: 2;
      grid-column-end: 5;
    }    

    .btn {
      grid-row-start: 3;
    }

    .ramform-item {
      flex-basis: 40%;
      flex-grow: 1;
    }

    .ramform-text__input {
      flex-basis: 50%;
      flex-grow: 1;
    }

    .consult__text {
      flex-basis: 50%;
      flex-grow: 1;
      margin-bottom: 20px;
      font-size: 16px;      
      line-height: 26px;
      color: $tw-black;

      @media (min-width: $screen-md) {
        margin-bottom: 0;
        margin-left: 40px;
        font-size: 18px;
        line-height: 28px;
      }      
    }

    .ramform-item-4 {
      position: static;
      display: block;
      padding: 0 !important;
      background-color: transparent !important;
      max-width: none !important;   
      height: auto !important;   
    }
    .btn {
      flex-basis: 100%;
    }

    .ramform-button {
      border-radius: 78px;
    }

    .ramform-button__title {
      font-size: 18px;
      line-height: 28px;
    }    
  }


  &--form,
  &--form-3 {
    .ramform-item-4 {
      position: static;
      display: block;
      padding: 0 !important;
      background-color: transparent !important;
      max-width: none !important;
      height: auto !important;
      margin-bottom: 0 !important;
    }


    .ramform-button {
      cursor: pointer;
      border-radius: 78px;
      &:hover,
      &:focus,
      &:active {
        background-color: $tw-black;
        border-color: $tw-black;
        color: #fff !important;
        text-decoration: none !important;
      }      
    }    

    .ramform-button__title {
      font-size: 18px !important;
      line-height: 28px !important;
    }  
  }

}
