// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.action-btn {

  $block-name: &; // #{$block-name}__element

  width: 36px;
  height: 36px;
  position: relative;
  overflow: hidden;
  border-width: 0;
  background-color: transparent;

  &__inner {
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);

    svg {
      fill: none;
    }
  }

  &--user {
      border-radius: 50%;
      &:hover {
        background-color: $tw-gray-100;
      }
  }

  &--search {
      border-radius: 50%;
      &:hover {
        background-color: $tw-gray-100;
      }
  }
}
