// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.product-b2b {

  $block-name:                &; // #{$block-name}__element

    &__item {
      display: flex !important;
      align-items: center;
      padding: 10px 12px !important;
      margin-bottom: 8px !important;
      border: 1px solid $tw-gray-200;
      border-radius: 10px;
      cursor: pointer;

      &:last-child {
        margin-bottom: 0;
      }

      .btn--buy {
        padding: 8px 16px !important;
        color: #fff;
        border-color: #0075da;
        background-color: #0075da;

        svg {
          margin-right: 8px !important;
        }

        span {
          color: #fff;
          font-size: 16px;
        }

        &:hover,
        &:focus,
        &:active {
          border-color: #0075da;
          background-color: #0075da;

          span {
            color: #fff;
          }
        }
      }
    }

    &__img {
    width: 190px;
    height: 50px;
    flex-shrink: 0;
    margin-right: 16px !important;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
    }
  }

  &__desc {
    margin-right: 16px !important;
    color: $tw-gray-500;
    font-size: 14px;
    line-height: 19px;

    span {
      white-space: nowrap;
      font-weight: 700;
    }
  }

  .btn--b2b {
    margin-left: 15px;
  }
}
