//        - compare-slider,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.compare-slider {

  $block-name:                &; // #{$block-name}__element


  &__after {
    position: relative;
    width: 100%;

    @media (min-width: $screen-md) {
      display: flex;
      justify-content: space-between;
      width: auto;
      height: auto !important;
    }
  }

  &__before {
    // position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;

    @media (min-width: $screen-md) {
      position: static !important;
      width: 48% !important;
      order: -1;
    }



    // img {
    //   width: auto;
    //   height: auto;
    //   max-width: none;
    // }
  }

  &__content {
    display: block;
    vertical-align: middle;
    width: 100%;
    height: 100%;
    border-radius: 8px;
    overflow: hidden;

    @media (min-width: $screen-md) {
      height: auto;

      &--after {
        width: 48% !important;
      }

      &--before {
        width: auto !important;
      }
    }
  }

  &__slider {
    position: absolute;
    width: 4px;
    height: 100%;
    top: 0;
    background-color: #2196F3;
    cursor: ew-resize;
    z-index: 9;

    @media (min-width: $screen-md) {
      display: none;
    }
  }

  &__slider-handle {
    position: absolute;
    top: 50%;
    width: 40px;
    height: 40px;
    margin-left: 2px;
    background-color: #2196F3;
    border-radius: 50%;
    cursor: ew-resize;
    transform: translate(-50%);
    background-image: url('#{$path-images}/arrows-move-h-white.svg');
    background-repeat: no-repeat;
    background-position: center;
  }

  .compare-card {
    &__image {
      height: 270px;
    }
  }
}

