//        - service,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.service {

  $block-name: &; // #{$block-name}__element

  @media (min-width: $onepage-width) {
    height: 100%;
  }

  &__inner {
    @media (min-width: 700px) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: auto 1fr;
    }

    @media (min-width: $onepage-width) {
      height: 100%;
      grid-template-rows: 30% 70%;
    }

    @media (min-width: $screen-xxl) {
      grid-template-columns: 664px 70px 1fr;
    }
  }

  &__img {
    height: 192px;
    border-radius: 4px;
    overflow: hidden;

    @media (min-width: 480px) {
      height: 300px;
    }

    @media (min-width: 700px) {
      height: 389px;
      grid-row-start: 1;
      grid-row-end: 3;
      grid-column-start: 1;
      grid-column-end: 2;
      border-radius: 0;
    }

    @media (min-width: $onepage-width) {
      height: 100%;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .section__title {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2;

    @media (min-width: 700px) {
      margin-top: 60px;
      margin-left: 30px;
    }

    @media (min-width: $onepage-width) {
      margin-top: auto;
    }

    @media (min-width: $screen-xxl) {
      grid-column-start: 3;
      grid-column-end: 4;
      margin-bottom: 40px;
      margin-left: 0;
    }
  }

  &__info {
    @media (min-width: 700px) {
      margin-left: 30px;
      grid-column-start: 2;
      grid-column-end: 3;
      grid-row-start: 2;
      grid-row-end: 3;
    }

    @media (min-width: $screen-xxl) {
      grid-column-start: 3;
      grid-column-end: 4;
      margin-left: 0;
    }
  }

  &__item {
    border-bottom: 1px solid $tw-gray-200;

    &:last-child {
      border-bottom: none;
    }
  }
}
