// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.phone {

  $block-name:                &; // #{$block-name}__element

  a {
    color: currentColor;
    text-decoration: none;

    &:hover {
      color: currentColor;
    }
  }

  &:hover,
  &:focus {
    color: currentColor;
  }

}
