//        - article-table,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.article-table {

  $block-name: &; // #{$block-name}__element

  border-collapse: collapse;
  width: 100%;
  @apply text-base;

  @media (max-width: 600px) {
    display: block;
  }

  tr,
  thead,
  tbody {
    @media (max-width: 600px) {
      display: block;
      margin-bottom: 15px;
    }
  }

  th {
    padding: 10px;
    background-color: $tw-gray-75;

    @media (max-width: 600px) {
      display: none;
    }
  }

  td {
    padding: 9px 10px;
    text-align: center;
    line-height: 1.2;

    @media (max-width: 600px) {
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-bottom: -1px;
      padding-left: 50%;
      text-align: left;
      min-height: 45px;
    }

    &::before {
      @media (max-width: 600px) {
        content: attr(data-content);
        position: absolute;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 45%;
        min-height: 45px;
        height: 100%;
        overflow: hidden;
        padding: 9px 10px;
        text-align: center;
        border-right: 1px solid $tw-gray-200;
        border-bottom: 1px solid $tw-gray-200;
        border-top: 1px solid $tw-gray-200;
        background-color: $tw-gray-75;
        color: $tw-black;
      }
    }
  }

  th,
  td {
    border: 1px solid $tw-gray-200;
  }

  &--col-2  {
    @media (max-width: 600px) {
      th {
        display: block !important;
      }

      tr {
        display: flex;
      }

      td {
        display: block;
        width: 50%;
        flex-basis: 50%;
        padding-left: 10px;

        &:before {
          display: none;
        }
      }

      thead th {
        width: 100%;
      }
    }
  }

  &--col-3  {
    @media (max-width: 600px) {
      font-size: 14px;
      line-height: 1.2;

      thead {
        margin-bottom: 0;
        tr {
          margin-bottom: 0;
        }
      }

      th {
        display: block !important;
        width: 30%;
        flex-grow: 1;

        &:first-child {
          display: none;
        }

        &:last-child {
          display: none !important;
        }
      }

      tr {
        display: flex;
        flex-wrap: wrap;

        &:first-child td:first-child {
          display: none;
        }
      }

      td {
        display: block;
        flex-basis: 30%;
        flex-grow: 1;
        padding-left: 0;
        text-align: center;

        &::before {
          display: none;
        }
      }

      td:first-child {
        width: 100%;
        flex-basis: 100%;
      }
    }
  }

}


