//        - footer-nav,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.footer-nav {

  $block-name: &; // #{$block-name}__element

  .page-footer & {
    a {
      color: $tw-gray-500;
      font-size: 14px;
      line-height: 2.3em;

      &:hover,
      &:focus {
        color: $tw-white;
      }
    }
  }

  grid-row-start: 1;
  grid-row-end: 2;

  @media (min-width: $screen-xl) {
    grid-row-start: 1;
    grid-row-end: 3;
  }

  @media (min-width: $screen-xxl) {
    grid-row-end: 3;
  }
  // @media (min-width: $screen-xl) {
  //   gap: 0 25px;
  // }

  &__list {
    list-style: none;
  }

  &__item {
    @media (min-width: $screen-lg) {
      margin-bottom: 8px;
    }
    @media (min-width: $onepage-width) {
      .screen-low & {
        margin-bottom: 0;
      }
    }
    @media (min-width: $screen-xl) {
      margin-bottom: 11px;
      .screen-low & {
        margin-bottom: 5px;
      }
    }
  }

  &__title {
    @media (min-width: $onepage-width) {
      .screen-low & {
        margin-top: 5px;
        margin-bottom: 10px;
      }
    }
  }

  &--catalog {
    grid-column-start: 1;
    grid-column-end: 2;
  }

  &--customer {
    grid-column-start: 3;
    grid-column-end: 4;
  }

  &--info {
    grid-column-start: 5;
    grid-column-end: 6;
  }

  &--coop {
    grid-row-start: 1;
    grid-row-end: 2;
    grid-column-start: 7;
    grid-column-end: 8;

    @media (min-width: $screen-lg) {
      display: flex;
      justify-content: flex-end;

      #{$block_name}__inner {
        min-width: 320px;
      }
    }

    @media (min-width: $screen-xl) {
      justify-self: start;
    }

    @media (min-width: $screen-xxl) {
      justify-self: stretch;
    }

  }
}
