    grid-template-rows: auto auto 1fr;
    grid-template-columns: auto;
  }

  .tns-outer {
    overflow: hidden;
  }

  &.product-catalog--inner-page .tns-outer {
    overflow: visible;
  }

  

  .tns-ovh {
    max-height: 100%;
    height: auto;
  }

  .btn {
    @media (min-width: $onepage-width) {
      grid-row-start: 2;
      grid-row-end: 3;
      justify-self: flex-start;
    }
  }

  .btn--simple {
    color: $tw-black !important;
    font-weight: 400 !important;
    text-decoration: none !important;
  }

  &.product-catalog--inner-page {
    overflow: hidden;
    .tns-outer {
      //!!! overflow: visible;
      position: relative;
    }
    .tns-controls {
      top: -60px;
    }
  }
}

