  display: none;
}


.img-cover {
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.img-contain {
  img {
    width: 100%;
    height: 100%;
    object-fit: scale-down;
    object-position: top right;
  }
}

.hide-text {
